import React from 'react';
import './element.css';


class Element extends React.Component{
    constructor(props){
        super(props);
        this.state={

        }   
    }

    signColor(symbol){
        const color = {
            'h':'#e9db39',
            'he': '#4e1892',
            'li': '#dc143c',
            'be': '#2578b5',
            'b': '#cc3536',
            'al': '#0041a5',
            'f': '#005b5a',
            //'sc': '#ce9335', // 旧 old
            'sc': '#ff9c00',  // 新配色 new color
            'la': '#a03e28',
            'ac': '#6a6834',
            'ubu': '#fff',
        }
        let gradient = `-webkit-gradient(linear, 100% 0%, 0% 0%,from(transparent), to(${color[symbol]})`
        console.log(symbol,gradient)
        let style = {'background':gradient}; 
        // let style = {'background-color':'#6a6834'}
        return style;
    }

    render(){
        if(this.props.metadata){
            const elem = (
                <div className="e-element">
                    <span className="e-number" style={this.signColor(this.props.metadata[4])}>{this.props.metadata[0]}</span>
                    <span className="e-symbol">{this.props.metadata[1]}</span>
                    <span className="e-name">{this.props.metadata[2]}</span>
                    <span className="e-bottomdata">{this.props.metadata[3]}</span>
                </div>
            )
            return elem
        }else{
            const elem = (
                <div className="e-element"/>
            )
            return elem
        }

        
    }
}

export default Element;
// module.exports = Element;